import { mapGetters,mapActions } from 'vuex'
export default {
  components: {

    },
  data(){
    return{
      count:0,
      in_cart:false,
    }
  },
  name: "catalog_card",
  props:['product'],
  watch:{
    count(nv){
      if(nv<0){
        this.count=0;
      }
    },
    cart(nv){
      this.checkInCart();
    }
  },
   mounted() {
    this.checkInCart();
  },
  computed:{
     ...mapGetters({
        cart:'cart/cart',
      }),
  },
  methods:{
     ...mapActions({
           ADD_TO_CART: 'cart/ADD_TO_CART'
        }),
    addToCart(){
        let cart = {count:this.count, product:this.product, id:this.product.id}
        this.ADD_TO_CART(cart);
        this.in_cart = true;
        this.count = 0;
         this.$message({message:'Добавлено', type: 'success'});
    },
    checkInCart(){
       let self = this;
      let ccc = this.cart.find(
          function (x){
            if(x.id===self.product.id){
              return x
            }
          }
        );
      if(ccc!==undefined){
        this.in_cart = true;
      }else{
        this.in_cart = false;
      }
    }
  },
}
