import Catalog_card from "../components/catalog/catalog_card";
export default {
  name: "search",
  components:{
    Catalog_card,
  },
  data(){
    return{
      show:false,
      products:[],
      count:0,
      currentPage:1,
      limit:16,
      search:null,
      offset:0,
    }
  },
  async fetch(){
    this.currentPage = this.$route.query['page']?this.$route.query['page']:1;
    this.search = this.$route.query['search']?this.$route.query['search']:null;
    if(this.search){
      this.search = encodeURI(this.search);
    }
    await this.getRes()
  },
  watch:{
    '$route.query'(nv){
      this.currentPage = nv.page?nv.page:1;
      this.search = nv.search?nv.search:null;
      this.getRes()
    }
  },
   mounted() {
    this.onScroll();
   window.addEventListener("scroll", this.onScroll)
  },
   beforeDestroy() {
      window.removeEventListener("scroll", this.onScroll)
      },
  methods:{
     onScroll(e) {
        var btn = this.$refs.upBtn;
        if (window.top.scrollY < 450) {
          btn.classList.add('hidebtn')
        } else {
          btn.classList.remove('hidebtn')
        }
    },
    moveTo () {
        let to = 0
        window.scroll({
          top: to,
          left: 0,
          behavior: 'smooth'
        })
      },
    async getRes(){
      this.show = true;
      let search = "";
      if(this.search!=null){
        search +=`&search=${this.search}`;
      }
      this.offset = (this.currentPage-1)*this.limit;
      let data = await this.$axios.get(`/product/product/?limit=${this.limit}&offset=${this.offset}${search}`);
      this.count = data.data.count;
      this.products = data.data.results;
      this.show = false;
    },
    paginate(){
      this.$router.push({ path: this.$route.path,
        query: { page: this.currentPage, filter: this.$route.query.filter?this.$route.query.filter:[] }})
      this.getRes()
      this.moveTo()
    },
  },
}
